// Navigation Styles here

nav {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 0 auto;
    border-bottom: 2px dotted black;

    .logo {
        width: 40%;
        margin: auto 0 auto 6%;
    }

    .links {
        width: 40%;
        display: flex;
        justify-content: space-evenly;
        margin: auto 0;
        font-size: 1.3rem;
    }

    a {
        text-decoration: none;
    }
}